#nav-wrapper {
    background-color: var(--primary-color);
    padding: 1%;
    margin: 0px;
    border-radius: 10px;
    display: flex;
    justify-content: space-between;
    position: sticky;
    top: 3px;
    opacity: 95%;
}

#right-nav {
    display: flex;
    justify-content: right;
    align-items: center;
}

#left-nav{
    display: flex;
    justify-content: left;
    align-items: center;
}

.link {
    margin: 0%;
    padding: 4px;
    padding-left: 25px;
    color: var(--text-color);
    font-weight: bold;
    text-decoration: none;
    color: var(--secondary-color);
}

.link.home-link {
    padding-left: 0px;
    font-weight: bolder;
    font-size: 160%;
    color: var(--accent-color);
}